html {
  scroll-behavior: smooth;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "FuturaPTDemi";
  font-style: normal;
  color: white;
  line-height: 1.2;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


*,
*::before,
*::after {
  box-sizing: inherit;
}

@font-face {
  font-family: "FuturaPTBold";
  font-style: normal;
  font-weight: 600;
  src: local("FuturaPTBold"), url(./assets/fonts/FuturaPT-Bold.woff) format("truetype");
}

@font-face {
  font-family: "FuturaPTDemi";
  font-style: normal;
  font-weight: 600;
  src: local("FuturaPTDemi"), url(./assets/fonts/FuturaPT-Demi.woff) format("truetype");
}

@font-face {
  font-family: "FuturaPTMedium";
  font-style: normal;
  font-weight: 400;
  src: local("FuturaPTMedium"), url(./assets/fonts/FuturaPT-Medium.woff) format("truetype");
}

@font-face {
  font-family: "FuturaPTBook";
  font-style: normal;
  font-weight: 400;
  src: local("FuturaPTBook"), url(./assets/fonts/FuturaPT-Book.woff) format("truetype");
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}